<template>
  <v-app id="inspire">
    <v-main class="blue-grey lighten-1">
      <v-container style="max-width: 450px" fill-height>
        <v-layout align-center row wrap>
          <v-flex xs12>
            <v-card>
              <div class="pa-5">
                <h1 style="text-align: center" class="mb-5">Login</h1>
                <form>
                  <v-text-field
                    label ="ID"
                    v-model="userId"
                    prepend-inner-icon="mdi-account">
                    </v-text-field>
                  <v-text-field
                    prepend-inner-icon="mdi-lock"
                    type="password"
                    label="Password"
                    v-model="password"
                  >
                  </v-text-field>
                    <v-btn
                      type="submit"
                      color="blue lighten-2 text-capitalize"
                      depressed
                      large
                      block
                      dark
                      class="mb-3"
                      @click="loginSubmit"
                      >
                      Sign up
                    </v-btn>
                </form>
              </div>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>

export default {
  data: () => {
    return{
      userId : null,
      password : null,
    }
  },
  methods: {
    loginSubmit(){
      const UserDTO = {};
      UserDTO.userId = this.userId;
      UserDTO.password = this.password;

      try{
        const url = "http://localhost:8080/login"
        this.$http.post(url,JSON.stringify(UserDTO),{
          headers : {
            "Content-Type": `application/json`,
          }
        })
        .then((response) =>{
          if(response.status === 200){
            console.log("login success")
          }
        })
      }catch(error){
        console.error(error);
      }

    }
  },

}

</script>